import React from 'react'

// import Heading from '../../../components/Heading'

import bottomCircle from './bottomCircle.png'
import Start from '../../../components/Start'

const PredictStart = ({ onNext }) => {
  return (
    <Start
      title="十大投資預測"
      subTitle="帶您掌握投資脈動"
      arrowColor="prussianBlue"
      bottomCircle={bottomCircle}
      onClick={onNext}
    />
  )
}

export default PredictStart
