import React from 'react'

import Box from './Box'
import Flex from './Flex'
import Text from './Text'
import Image from './Image'
import Button from './Button'
import { responsive } from './ThemeProvider/theme'
import Container from './Container'

const ContentModule = ({ src, text, source, buttonText, buttonBg, textTitle, title, background, sourceColor, url, ...props }) => {
  return (
    <Box background={background} {...props}>
      <Container py="1.75em">
        <Flex alignItems="center" justifyContent="flex-end" flexDirection={responsive('column', 'row')}>
          <Box width={responsive('100%', 35 / 100)} whiteSpace="pre-wrap" color="prussianBlue" mr={responsive(0, '2.75em')}>
            {textTitle && <Text.Bold pb="1em" fontSize={responsive('1em', '1.625em')}>{textTitle}</Text.Bold>}
            <Text fontSize={responsive('1em', '1.5em')}>{text}</Text>
            {buttonText && <Button.SingleSharp href={url} mt="1em" bg={buttonBg}>{buttonText}</Button.SingleSharp>}
          </Box>
          <Box width={responsive('100%', 1 / 2)}>
            <Text color="brightBlue" fontWeight="700" fontSize={responsive('1em', '1.25em')}>{title}</Text>
            {typeof src === 'function' ? src() : <Image src={src} />}
            {source &&
              <Box pt="0.5em" borderTop="1px solid" color={sourceColor} fontSize="0.875em">
                資料來源:{source}
              </Box>
            }
          </Box>
        </Flex>
      </Container>
    </Box>
  )
}

export default ContentModule
