import React from 'react'

import backgroundBg from './background.png'
import Box from '../Box'
import Flex from '../Flex'
import BackgroundImage from '../BackgroundImage'

const BeforeStart = ({ title, background, text, noCircle, children }) => {
  return (
    <Box position="relative" color="white" height="100vh" overflow="hidden">
      {noCircle && <BackgroundImage src={backgroundBg} opacity="0.35" />}
      <Flex position="absolute" left="50%" top="50%" transform="translate(-50%, -50%)">
        <Box background={background} width="2.5em" mr="2.5em" opacity="0.5" />
        {children}
      </Flex>
    </Box>
  )
}

export default BeforeStart
