import React, { useEffect, useRef, useState } from 'react'
import styled from '@emotion/styled'
import { get } from 'lodash'

import ArrowIcon from '../../../components/ArrowIcon'
import Box from '../../../components/Box'
import theme, { responsive } from '../../../components/ThemeProvider/theme'
import { ReactComponent } from './side-nav.svg'

const SVG = styled(ReactComponent)`
text {
  pointer-events: none;
}
.nav {
  cursor: pointer;
}
`

let svgParts

const SideNav = ({ bg, active, activePart, onClick }) => {
  const [isOpen, setIsOpen] = useState()
  const svgRef = useRef()
  useEffect(() => {
    svgParts = svgRef.current.querySelectorAll('.nav')
    svgParts.forEach((nav, i) => {
      nav.onclick = () => onClick(i)
    })
  }, [])
  useEffect(() => {
    svgParts.forEach(part => {
      part.setAttribute('fill', 'white')
      part.setAttribute('opacity', '0.7')
    })
    if (svgParts[activePart]) {
      svgParts[activePart].setAttribute('fill', get(theme, ['colors', bg]))
      svgParts[activePart].setAttribute('opacity', 1)
    }
  }, [activePart])
  return (
    <Box.Fixed
      width={responsive('20em', '25em', '30em')}
      bg={isOpen ? 'primary' : bg}
      rounded="full"
      left="0"
      top="50%"
      transform={`translate(-${isOpen ? 50 : (active ? 90 : 100)}%, -50%)`}
      transition="all 0.5s ease"
      onClick={() => setIsOpen(!isOpen)}
    >
      <SVG ref={svgRef} />
      <Box.Absolute top="50%" right="0.75em" transform="translateY(-50%)">
        <ArrowIcon
          color={isOpen ? 'primart' : 'white'}
          fontSize="1em"
          transition="all 0.5s ease"
          // transform={`rotate(${isOpen ? '180deg' : 0})`}
        />
      </Box.Absolute>
    </Box.Fixed>
  )
}

export default SideNav
