import React, { useState } from 'react'

import Box from '../Box'
import Flex from '../Flex'
import Text from '../Text'
import Image from '../Image'
import Button from '../Button'
import { responsive } from '../ThemeProvider/theme'

import CloseIcon from './CloseIcon'
import left from './left.svg'
import right from './right.svg'

const Title = ({ title, subTitle, text, note, explain, exTitle, exText }) => {
  const [openExplain, setExplain] = useState(true)
  return (
    <Flex color="white" position="relative">
      <Box width="50%">
        <Image src={left} bg="prussianBlue" />
      </Box>
      <Box width="50%" >
        <Image src={right} bg="prussianBlue" />
      </Box>
      <Box position="absolute" top="50%" left="50%" transform="translate(-50%, -50%)">
        <Text fontSize={responsive('1.5em', '3em')}>{title}</Text>
        <Text whiteSpace="pre-wrap" fontSize={responsive('1em', '2em')}>{subTitle}</Text>
        <Text whiteSpace="pre-wrap" fontSize={responsive('0.875em', '1.375em')}>{text}</Text>
        {note && <Text pt="0.5em" color="red" fontSize="0.875em">*{note}</Text>}
      </Box>
      {explain && openExplain && (
        <Box
          width="22.5%"
          position="absolute"
          bg="white"
          px="1.75em"
          pt="1.75em"
          pb="1.875em"
          borderRadius="0 0 0 3em"
          transform="translateY(-50%)"
          right="0"
          top="50%"
        >
          <Text color="prussianBlue" fontSize={responsive('1em', '1.25em')}>{exTitle}</Text>
          <Text color="prussianBlue" fontSize={responsive('0.75em', '1em')}>{exText}</Text>
          <Box position="absolute" left="-1em" top="-0.75em">
            <Button
              onClick={() => setExplain(false)}
              py="0"
              px="0"
              bg="transparent"
              _hover={{ bg: 'transparent'}}
            >
              <CloseIcon />
            </Button>
          </Box>
        </Box>
      )}
    </Flex>
  )
}

export default Title
