import React from 'react'
import Box from '../../../components/Box'
import Text from '../../../components/Text'
import Flex from '../../../components/Flex'
import ContentModule from '../../../components/ContentModule'
import Title from '../../../components/Title'
import Container from '../../../components/Container'
import { responsive } from '../../../components/ThemeProvider/theme'

import chart from './chart_2_1.svg'

const WeatherOne = () => {
  return (
    <Box bg="white">
      <Title subTitle={"氣候變化如何影響投資策略?\n投資機會在哪?"} />
      <ContentModule
        text={"巴黎氣候協定:\n\n承諾要將全球暖化控制在2度以下，這是相當艱難的挑戰，而且影響深遠，需要讓全球經濟與能源系統在未來幾十年完全轉型，才可能達到此目標。\n\n在全球擁抱替代能源與零碳排科技時，氣候投資機會也就此顯現。"}
        src={chart}
        source="IPCC  2018  施羅德投資"
      />
    </Box>
  )
}

export default WeatherOne
